@import 'components/variables';
@import 'components/filtros';

.section-alojamientos {
  padding: 5% 0;
  .title-page {
    font-size: 3.5rem;
    font-weight: 900;
    color: $violet;
  }
  .content-detalles {
    @media (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      
    }
    .fila1 {
      display: flex;
      align-items: center;
      margin-right: 1rem;
      p {
        margin-bottom: 0;
        color: $text-gray;
        font-size: 1.2rem;
        font-weight: bold;
      }
    }
    .fila2 {
      display: flex;
      align-items: center;
      margin-right: 1rem;
      .img-icons {
        width: 1.5rem;
        margin-right: .8rem;
      }
      p {
        margin-bottom: 0;
        padding-right: 1rem;
      }
    }
    .fila3 {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-right: 1rem;
      .img-icons {
        width: 2rem;
        margin-right: .8rem;
      }
      .form-select {
        width: 70px;
        border: none;
        padding: .375rem 1.4rem .375rem .75rem;
        @media (min-width: 425px) {
          width: 100px;
        }
        &:focus {
          border-color: $violet;
          outline: 0;
          box-shadow: 0 0 0 0.25rem rgba($color: $violet, $alpha: 0.5);
        }
      }
      .vistas {
        text-align: center;
        display: flex;
        .img-icons2 {
          width: 2rem;
          cursor: pointer;
        }
      }
    }
  }
  .img-destacada {
    position: relative;
    border: 1px solid $gray-light;
    .content {
      overflow: hidden;
      @media (min-width: 768px) {
        position: absolute;
        width: 100%;
        height: 100%;
      }
      @media (max-width: 767.98px) {
        height: 40vw;
      }
      .destacado {
        background-color: $orange;
        text-align: center;
        color: $white;
        font-weight: bold;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        font-size: .7rem;
        padding: .4rem;
        position: absolute;
        top: 15px;
        left: 0;
        @media (min-width: 768px) {
          font-size: .9rem;
        }
      }
      .img-ppal {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        transition: all 0.3s;
        &:hover {
          transform: scale(1.2);
        }
      }
      .heart {
        position: absolute;
        top: 20px;
        right: 0;
        width: 2rem;
        transform: translateX(-20px);
      }
    }
  }
  .detalles-hotel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid $gray-light;
    .valoracion {
      .valor {
        position: relative;
        background-color: $violet;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        color: white;
        font-size: 1.2rem;
        font-weight: 800;
        margin-right: 1rem;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: -10px;
          bottom: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent $violet transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
      }
      img {
        width: 10px;
        @media (min-width: 375px) {
          width: 15px;
        }
        @media (min-width: 482px) {
          width: 23px;
        }
      }
    }
    .name-hotel {
      font-size: 1.5rem;
      font-weight: bold;
      color: $text-gray;
      transition: all 0.3s;
      &:hover {
        color: $violet;
      }
      p {
        margin-top: 1rem;
      }
    }
    .ubicacion {
      display: flex;
      align-items: center;
      margin-top: 2rem;
      a {
        color: $violet;
        transition: all 0.3s;
        &:hover {
          color: $orange;
        }
      }
    }
    .ubicacion-vertical {
      display: flex;
      align-items: center;
      margin-top: 1rem;
      font-size: .8rem;
      img {
        width: 5%;
        margin-right: .4rem;
      }
    }
  }
  .price-hotel {
    border: 1px solid $gray-light;
    .price {
      font-size: 1.2rem;
      margin-bottom: 0;
    }
    .denominacion {
      font-weight: bold;
      strong {
        color: $violet;
        font-size: 1.7rem;
        font-weight: 700;
      }
    }
    .descrip-reserva {
      margin-bottom: 0;
    }
    .btn-violet-white {
      font-size: .8rem;
      margin-top: 2rem;
    }
  }
}