.filtro-desktop {
  .accordion-button:not(.collapsed) {
    color: $violet;
    font-weight: bold;
    background-color: #f0d5f8;
  }
  .accordion-button:not(.collapsed)::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%237d428f'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
  }
  .accordion-button {
    font-weight: bold;
    &:focus {
      border-color: $violet;
      box-shadow: none
    }
  }
  .accordion-item:last-of-type {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}
.clean-filtro {
  padding: 2rem;
  text-align: center;
  font-weight: bold;
  font-size: 1.3rem;
  border: 1px solid rgba(0, 0, 0, .125);
  border-top: 0;
  border-bottom-right-radius: .25rem;
  border-bottom-left-radius: .25rem;
  a {
    color: $text-gray;
  }
}
.search-more {
  position: relative;
  padding: 1rem;
  background-color: $violet;
  border-radius: 5px;
  .text-big {
    color: $white;
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 0;
    line-height: 1;
  }
  a {
    color: $white;
    font-size: .8rem;
    transition: all .3s;
    &:hover {
      color: $yellow;
    }
  }
  img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10%;
    @media (min-width: 992px) {
      width: 30%;
    }
  }
}